<template>
    <section class="pt-2">
        <div class="container">
            <div class="row row-grid" data-aos="fade-up" data-aos-anchor-placement="top-bottom" id="skills">
              <div class="col-lg-12">
                <h2>Compétences</h2>
                <p class="pb-0">Au vu des nombreux projets en équipe et de l'expériences professionelles j'ai acquis un savoir-faire dans un monde en constante évolution.</p>
              </div>
              <div class="pt-5 col-lg-12">
                <ul class="skills-bar-container">
                  <li v-for="item in skills">
                    <div class="progressbar-title">
                      <h4>{{ item.type }}</h4>
                      <span class="percent" id="html-pourcent">{{ item.value }}</span>
                    </div>
                    <div class="bar-container">
                      <span v-bind:class="activeSkills"  v-bind:style='"--width:" + item.value + "; --colors:" + item.colors'></span>
                    </div>
                  </li>
                </ul>
              </div>
            </div>
            <div class="row row-grid" data-aos="fade-up" data-aos-anchor-placement="top-bottom" id="frameworks">
              <div class="col-lg-12 col-md-12 col-xs-12">
                <h2>Frameworks / Library</h2>
                <p class="pb-0">Pour en apprendre toujours davantage et être plus productif. Se focaliser sur le meilleurs.</p>
              </div>
              <div class="pt-5 col-lg-12">
                <ul class="skills-bar-container">
                  <li v-for="item in frameworks">
                    <div class="progressbar-title">
                      <h4>{{ item.type }}</h4>
                      <span class="percent">{{ item.value }}</span>
                    </div>
                    <div class="bar-container">
                      <span v-bind:class="activeFrameworks"  v-bind:style='"--width:" + item.value + "; --colors:" + item.colors'></span>
                    </div>
                  </li>
                </ul>
              </div>
            </div>
        </div>
    </section>
</template>
<script>
export default {
  data() {
    return {
      activeSkills: "progressbar",
      activeFrameworks: "progressbar",
      skills: [
        {
          type: "HTML",
          colors: "#c0392b",
          value: "95%"
        },
        {
          type: "CSS",
          colors: "#1199ff",
          value: "90%"
        },
        {
          type: "Sass",
          colors: "#27ae60",
          value: "85%"
        },
        {
          type: "Javascript",
          colors: "#9b59b6",
          value: "80%"
        },
        {
          type: "Node JS",
          colors: "#96E6B3",
          value: "80%"
        },
        {
          type: "Git",
          colors: "#ffa500",
          value: "75%"
        },
        {
          type: "PHP",
          colors: "#FFFD82",
          value: "60%"
        }
      ],
      frameworks:[
        {
          type: "ReactJs",
          colors: "#ffa500",
          value: "90%"
        },
        {
          type: "Vue.js",
          colors: "#27ae60",
          value: "65%"
        },
        {
          type: "MeteorJs",
          colors: "#2B9EB3",
          value: "80%"
        },
        {
          type: "CodeIgniter",
          colors: "#FFFD82",
          value: "70%"
        }
      ]
    }
  },
  created() {
    window.addEventListener("scroll", this.scrollEvent);
  },
  methods: {
    scrollEvent(){
      const skills = document.getElementById('skills');
      if(skills.className === "row row-grid aos-init aos-animate"){
        this.activeSkills = "progressbar active";
      }
      const frameworks = document.getElementById('frameworks');
      if(frameworks.className === "row row-grid aos-init aos-animate"){
        this.activeFrameworks = "progressbar active";
      }
    }
  }
};
</script>



