import Vue from 'vue'
import App from './App.vue'
import Argon from "./plugins/argon-kit"
import AOS from 'aos'
import 'aos/dist/aos.css'

Vue.use(Argon);
new Vue({
  el: '#app',
  created () {
    AOS.init({
      offset: 120
    })
  },
  render: h => h(App)
})



