<template>
    <div class="profile-page">
        <section class="section-profile-cover section-shaped my-0">
            <div class="shape shape-style-1 shape-primary shape-skew alpha-4">
            </div>
        </section>
        <section class="section section-skew">
            <div class="container">
                <card shadow class="card-profile mt--300" no-body>
                    <div class="px-4">
                        <div class="row justify-content-center">
                            <div class="col-lg-3 order-lg-2">
                                <div class="card-profile-image">
                                    <a href="#">
                                        <img src="@/assets/moi_dev.jpg" class="rounded-circle">
                                    </a>
                                </div>
                            </div>
                            <div class="col-lg-4 order-lg-3 text-lg-right align-self-lg-center">
                                <div class="card-profile-actions py-4 mt-lg-0">
                                    <base-button type="info" size="sm" class="mr-4" @click="socialLink">Connect</base-button>
                                    <base-button type="default" size="sm" class="float-right" @click="message">Message</base-button>
                                </div>
                            </div>
                            <div class="col-lg-4 order-lg-1">
                                <div class="card-profile-stats d-flex justify-content-center">
                                    <div>
                                        <span class="heading"><i class="fa fa-phone"></i></span>
                                        <span class="description">06 63 37 54 27</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="text-center mt-5">
                            <h3>Valentin Messiaen
                                <span class="font-weight-light">, 26</span>
                            </h3>
                            <div class="h6 font-weight-300"><i class="ni location_pin mr-2"></i>Lille, France</div>
                            <div class="h6 mt-4"><i class="ni business_briefcase-24 mr-2"></i>Développeur - Front End</div>
                            <div><i class="ni education_hat mr-2"></i>Imagination, création</div>
                        </div>
                        <div class="mt-5 py-5 border-top text-center">
                            <div class="row justify-content-center" data-aos="fade-up" data-aos-anchor-placement="top-bottom">
                                <div class="col-lg-9">
                                    <p>Le code est une enigme à résoudre, un challenge qui me permet d'évoluer et d'avancer en apprenant tous les jours.</p>
                                    <p>Passionné par les nouvelles technologies, je reste en veille constante sur les prochaines tendance et les nouvelles méthodes.
                                    Soif de savoir et de réactivité, je sais m'adapter facilement à de nouveaux projets.</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </card>
            </div>
        </section>
    </div>
</template>
<script>
export default {
  methods: {
    socialLink: () => {
      window.open('https://www.linkedin.com/in/valentin-messiaen-2aa650a9/', '_blank');
    },
    message: () => {
      window.location.href = 'mailto:valentin.messiaen@gmail.com?subject=Votre sujet&body=Que puis-je faire pour vous ?';
    }
  }
};
</script>
<style>
</style>



