<template>
  <section class="section section-lg">
      <div class="container" data-aos="fade-up" data-aos-anchor-placement="top-bottom">
          <card gradient="success"
                no-body
                shadow-size="lg"
                class="border-0">
              <div class="p-5">
                  <div class="row align-items-center">
                      <div class="col-lg-8">
                          <h3 class="text-white">Obtenir mon Curriculum Vitae</h3>
                          <p class="lead text-white mt-3">Envie d'en savoir plus sur mes compétences ou mon parcours ?</p>
                      </div>
                      <div class="col-lg-3 ml-lg-auto">
                          <base-button
                            tag="a"
                            href="https://drive.google.com/uc?export=download&id=1PH1h4u6xDyHaQxSxbY0JZn5LM5ofSOIw"
                            type="white"
                            block
                            size="lg">
                              Télécharger mon CV
                          </base-button>
                      </div>
                  </div>
              </div>
          </card>
      </div>
  </section>
</template>

<script>
import AOS from 'aos'
export default {
  created() {
    AOS.init();
  }
};
</script>



