<template>
  <div class="Loader" v-if="load">
    <div class="Bar"></div>
    <div class="Bar"></div>
    <div class="Bar"></div>
    <div class="Bar"></div>
    <div class="Bar"></div>
    <div class="Bar"></div>
  </div>
  <div id="app" v-else>
    <homepage></homepage>
  </div>
</template>

<script>
import { FadeTransition } from "vue2-transitions";
import Homepage from "./views/Components.vue";

export default {
  name: 'app',
  components: {
    FadeTransition,
    Homepage
  },
  data(){
    return {
      load: true
    }
  },
  mounted(){
    const _this = this;
    setTimeout(function(){
      _this.load = false;
    }, 3000);
  }
}
</script>



