<template>
    <header class="header-global">
      <div class="progress-bar--scroll" v-bind:style="'--scroll:' + scrollProgress"></div>
      <Menu></Menu>
    </header>
</template>
<script>
import BaseNav from "../components/BaseNav";
import CloseButton from "../components/CloseButton";
import Menu from "../views/components/Navigation.vue";

export default {
  components: {
    BaseNav,
    CloseButton,
    Menu
  },
  data() {
    return {
      scrollProgress: 0
    }
  },
  created() {
    this.progessBarInit();
  },
  methods: {
    progessBarInit(){
      const doc = document.documentElement;
      const body = document.body;
      let scroll;
      const _this = this;
      document.addEventListener('scroll', function() {
        scroll = (doc['scrollTop'] || body['scrollTop']) / ((doc['scrollHeight'] || body['scrollHeight']) - doc.clientHeight);
        _this.scrollProgress = scroll;
      });
    }
  }
};
</script>
<style>
</style>



