<template>
    <section class="section section-components pb-0" id="section-components">
        <div class="container">
            <div class="row">
                <div class="col-lg-12 text-center">
                    <!-- Basic elements -->
                    <!-- <h2 class="mb-5">
                        <span>Contact</span>
                    </h2> -->
                    <!-- Buttons -->
                    <!-- Button styles -->
                    <div>
                      <base-button class="btn-3" type="primary" icon="ni ni-email-83" @click="message">Contactez-moi</base-button>
                    </div>
                </div>
            </div>
        </div>
    </section>

</template>
<script>
export default {
  methods: {
    message: () => {
      window.location.href = 'mailto:valentin.messiaen@gmail.com?subject=Votre sujet&body=Que puis-je faire pour vous ?';
    }
  }
};
</script>



